import React from "react";
import { Container } from "react-bootstrap";
import Scissors from "../images/scissors.gif";
import Paper from "../images/paper.gif";
import Rock from "../images/rock.gif";
import RPS from "../images/rps.gif";
import "./Cachipun.css";
import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

function Cachipun(props) {
  const styleButton = {
    borderRadius: 25,
  };




  return (
    <div>
      <div
        className="row"
        style={{ marginTop: 100, marginLeft: 50, marginRight: 50 }}
      >
        <div className="col-4">
          <button
            onClick={() => props.onAddOption("piedra")}
            style={styleButton}
          >
            <img src={Rock} width="300" height="300" style={styleButton} />
          </button>
        </div>

        <div className="col-4">
          <button
            onClick={() => props.onAddOption("papel")}
            style={styleButton}
          >
            <img src={Paper} width="300" height="300" style={styleButton} />
          </button>
        </div>

        <div className="col-4">
          <button
            onClick={() => props.onAddOption("tijera")}
            style={styleButton}
          >
            <img src={Scissors} width="300" style={styleButton} />
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col-6">
          <h2 className="mt-5 text-center text-primary">
            Your score <h1>{props.score}</h1>
          </h2>
        </div>
        <div className="col-6">
          <h2 className="mt-5 text-center text-primary">
            The Machine's score <h1>{props.pcScore}</h1>
          </h2>
        </div>
      </div>

      {props.load === false ? (
        <Container
          className="row"
          style={{ marginTop: 30, marginLeft: 50, marginRight: 50 }}
        >
          <div className="col d-flex justify-content-center">
            <div style={styleButton}>
              {props.myChoice === "piedra" && (
                <img
                  src={Rock}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-right"
                  data-aos-duration="1500"
                />
              )}
              {props.myChoice === "papel" && (
                <img
                  src={Paper}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-right"
                  data-aos-duration="1500"
                />
              )}
              {props.myChoice === "tijera" && (
                <img
                  src={Scissors}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-right"
                  data-aos-duration="1500"
                />
              )}
            </div>
          </div>

          <div className="col ">
            <div style={styleButton}>
              {props.pcChoice === "piedra" && (
                <img
                  src={Rock}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-left"
                  data-aos-duration="1500"
                />
              )}
              {props.pcChoice === "papel" && (
                <img
                  src={Paper}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-left"
                  data-aos-duration="1500"
                />
              )}
              {props.pcChoice === "tijera" && (
                <img
                  src={Scissors}
                  width="300"
                  height="300"
                  style={styleButton}
                  data-aos="fade-left"
                  data-aos-duration="1500"
                />
              )}
            </div>
          </div>
        </Container>
      ) : (
        <div className="d-flex justify-content-center">
          <img
            src={RPS}
            width="300"
            height="300"
            style={styleButton}
            data-aos="zoom-in"
            data-aos-duration="1500"
          />
        </div>
      )}
    </div>
  );
}

export default Cachipun;
