import React, { useState } from "react";
import Cachipun from "./Cachipun";
import { Button } from "react-bootstrap";

function CachipunContainer(props) {
  const [myChoice, setMyChoice] = useState("");
  const [result, setResult] = useState("start");
  const [pcChoice, setPcChoice] = useState("0");
  const [score, setScore] = useState(0);
  const [pcScore, setPcScore] = useState(0);
  const [gameCounter, setGameCounter] = useState(0);
  const [load, setLoad] = useState(false)

  const onHandleButton = (e) => {
    // e.preventDefault();
    const arr = ["piedra", "papel", "tijera"];
    setLoad(true)
    setTimeout(() => {
      const pcChoiceRandom = arr[Math.floor(Math.random() * 3)];

      const finalChoice = e;
      setGameCounter(gameCounter + 1);

      if (finalChoice === "piedra" && pcChoiceRandom === "papel") {
        setResult("lose");
        setPcScore(pcScore + 1);
      } else if (finalChoice === "piedra" && pcChoiceRandom === "tijera") {
        setResult("win");
        setScore(score + 1);
      } else if (finalChoice === "papel" && pcChoiceRandom === "piedra") {
        setResult("win");
        setScore(score + 1);
      } else if (finalChoice === "papel" && pcChoiceRandom === "tijera") {
        setResult("lose");
        setPcScore(pcScore + 1);
      } else if (finalChoice === "tijera" && pcChoiceRandom === "piedra") {
        setResult("lose");
        setPcScore(pcScore + 1);
      } else if (finalChoice === "tijera" && pcChoiceRandom === "papel") {
        setResult("win");
        setScore(score + 1);
      } else if (finalChoice === pcChoiceRandom) {
        setResult("draw");
      }
      setPcChoice(pcChoiceRandom);
      setMyChoice(finalChoice);
      setLoad(false)
    }, 2000);
  };
  return (
    <div>
      {gameCounter < 5 && (
        <Cachipun
          myChoice={myChoice}
          result={result}
          pcChoice={pcChoice}
          onAddOption={onHandleButton}
          score={score}
          pcScore={pcScore}
          load={load}
        />
      )}
      <div className="row">
        {gameCounter >= 5 && (
          <div style={{ marginTop: 200, marginLeft: 100 }}>
            <div style={{ fontFamily: "Lato" }}>
              
              {score >= 3 ? (
                <div>
                  <h3>YOU WON THE GAME!! YOUR SCORE WAS {score} :)</h3>
                  <img
                    src="https://media.giphy.com/media/3oriO6RwVAK6Yoq2WY/giphy.gif"
                    width="450"
                  />
                </div>
              ) : (
                <div>
                  <h3>YOU LOST THE GAME!! YOUR SCORE WAS {score}</h3>
                  <img
                    src="https://media.giphy.com/media/3osxYyAVLIq2wrEyeA/giphy.gif"
                    width="550"
                  />
                </div>
              )}
            </div>
            <div className="col">
              <Button
                className="mt-5"
                variant="outline-primary"
                value="tijera"
                size="lg"
                onClick={() => window.location.reload(false)}
              >
                TRY AGAIN
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default CachipunContainer;
